import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';

/*
    THE NAVIGATION BAR CANNOT BE DYNAMICALLY GENERATE BECAUSE OF THE STYLE ISSUE.
*/
class NavigationBar extends Component {
    render( ) {
        const { appTitle } = this.props;

        return (
            <div className="navbar fixed-top navbar-expand-lg navbar-dark bg-dark">
                <NavLink className="navbar-brand" to="/">S</NavLink>

                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#mainNav">
                    <span className="navbar-toggler-icon"></span>
                </button>

                <div className="collapse navbar-collapse" id="mainNav">
                    <ul className="navbar-nav">
                        <li className="nav-item">
                            <NavLink className="nav-link" to="/">Home</NavLink>
                        </li>

                        <li className="nav-item">
                            <NavLink className="nav-link" to="/login">Login</NavLink>
                        </li>

                        <li className="nav-item">
                            <NavLink className="nav-link" to="/register">Register</NavLink>
                        </li>
                    </ul>
                </div>
            </div>
        );
    }
}

export default NavigationBar;